// utils
@import "utils/h5bp";


body, html {
	height: 100%;
	background: gray;
}

.main-container {
	position: relative;
	margin: 0;
	height: 100%;
}

#game {
	position: relative;
	width: 414px;
	height: 736px;
	// border: 1px solid black;

	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	transform-origin: center center; 
}
#canvas {
	width: 100%;
	height: 100%; 
	background-color: #d0db56;
}
#world-selector {
	position: absolute;
	width: 50px;
	top: 90px;
	right: 5px;
	background-color: white;
	border-radius: 25px;
	-webkit-tap-highlight-color: rgba(0,0,0,0);
	-webkit-tap-highlight-color: transparent; 
	.lvl {
		position: relative;
		width: 40px;
		height: 40px;
		margin: 3px;
		border-radius: 50%;
		background-color: white;
		border: 2px solid black;
		cursor: pointer;
	}
}
